<template>
  <div v-if="list.length" class="post-view">
    <h3>我是列表</h3>
    <h3 v-for="item in list" :key="item.id">
      <!-- <router-link :to="`/post/${item.id}`">{{ item.title }}</router-link> -->
      <router-link
        :to="{ name: 'post', params: { id: item.id }, query: { a: 10 } }"
        >{{ item.title }}</router-link
      >
    </h3>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
    };
  },
  watch: {
    "$route.params.type": {
      async handler(type) {
        let http = "";
        if (type === "frontend" || type === undefined) {
          http = `?tab=${"ask"}`;
        } else if (type === "backend") {
          http = `?tab=${"share"}`;
        } else if (type === "recommend") {
          http = `?tab=${"all"}`;
        } else {
          http = `?tab=${type}`;
        }

        const res = await this.$http.get(`/topics/${http}`);
        this.list = res.data;
      },
      immediate: true,
    },
  },
};
</script>

